
$color-blue: #182b89;
$color-lightblue: #3ea6df;

$transition-2s: all 0.2s ease-in-out;


.wrap-prof-hero {
    width: 100%;
    height: 100vh;
    margin: -80px auto 0;
    background-image: linear-gradient(to top, rgba(255, 255, 255, 0.2), rgba(24, 43, 136, 1)), url('../../images/BG-15.png');
    background-repeat: no-repeat;
    background-position: bottom center; 
    background-size: cover;
    display: flex;
    // align-items: center;
    justify-content: center;
}

.prof-content-1 {
    width: 80%;
    max-width: 1600px;
    margin: 100px auto 0;
    & h1 {
        color: #fff;
        font-size: 24px;
    }
}

.prof-cont-item-1 {
    width: 450px;
    margin: 50px auto 0;
    padding: 60px 30px 50px;
    border-radius: 20px;
    backdrop-filter: blur(10px);
    background-color: rgba(238, 238, 238, 0.15);

    & > div {
        margin-bottom: 20px;
    }

    & h2 {
        color: #fff;
        font-size: 14px;
        font-weight: 600;
        margin-bottom: 5px;
    }
    
    & .profile-username, & .profile-email {
        & input {
            width: 100%;
            color: $color-blue;
            background-color: #fff;
            font-size: 14px;
            font-weight: normal;
            padding: 8px 20px;
            border: none;
            border-radius: 10px;
            outline: none;
            box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);
            -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) ;
            -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);
        }
    }
    
    & .profile-phone {
        & div {
            color: $color-blue;
            background-color: #fff;
            display: flex;
            align-items: center;
            padding: 8px 18px;
            font-size: 14px;
            border: none;
            border-radius: 10px;
            box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);
            -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) ;
            -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);
        }
        & input {
            color: $color-blue;
            width: 100%;
            margin-left: 5px;
            font-size: 14px;
            outline: 0;
            border: none;
        }
    }

    & .profile-edit-btn {
        text-align: center;
        & button {
            color: #fff;
            background-color: $color-lightblue;
            width: 100%;
            padding: 10px;
            margin: 20px 0 0;
            font-size: 14px;
            font-weight: bold;
            border: none;
            border-radius: 10px;
            outline: none;
            transition: $transition-2s;
            &:hover {
                background-color: #64b6e3;
            }
            &:nth-child(2) {
                background-color: #060c84;
                &:hover {
                    background-color: $color-blue;
                }
            }
            &:nth-child(3) {
                margin: 10px 0 0;
                background-color: $color-lightblue;
                &:hover {
                    background-color: #64b6e3;
                }
            }
        }
        & .hidden {
            display: none;
        }
    }
}


@media screen and (min-width: 1901px) {

    .prof-cont-item-1 {
        width: 450px;
        margin: 70px auto 0;
    }

}


@media screen and (max-width: 1200px) {


}



@media screen and (max-width: 1024px) {

    .wrap-prof-hero {
        height: fit-content;
        padding: 30px 0 120px;
    }

}



@media screen and (max-width: 576px) {
    .wrap-prof-hero {
        height: fit-content;
        padding: 0 0 90px;
    }

    .prof-content-1 {
        width: 90%;

        & h1 {
            font-size: 20px;
        }
    }
    
    .prof-cont-item-1 {
        width: 100%;
        margin: 30px auto 0;
        padding: 40px 30px;
    }

}